.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.gif-container {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 9999;
}

.gif-button {
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
}

.gif-button:focus {
  outline: 2px solid blue;
}

.gif-image {
  width: 50px;
  height: 50px;
}
.employee-list-container {
  max-height: 600px; /* Set the maximum height of the employee list container */
  overflow-y: auto; /* Enable vertical scrolling for the list container */
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
  height: 100vh;
}

.employee-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 20px;
  overflow-y: auto;
}

.employee-list-section-title {
  font-weight: bold;
  font-size: 1.2em; /* You can adjust the font size as needed */
  margin-bottom: 10px; /* Add some space below the heading */
}


.employee-list-item {
  padding: 10px;
  
  cursor: pointer;
  transition: background-color 0.3s;
  background: linear-gradient(to right, rgba(168, 119, 166, 0.8)0%, transparent 50%);
  background-size: 200% 100%;
  transition: background-position 0.3s;
  border-radius: 8px;
  overflow: hidden;
}

.employee-list-item div {
  margin-bottom: 5px; /* Add some space between details */
}

.employee-list-item div:not(.duration) {
  font-weight: bold;
  color: rgb(7, 7, 6); /* Change the color to your preferred highlight color */
}

.employee-list-item:hover {
  background-position: -100% 0;
}


.selected {
  background-color: rgba(0, 0, 0, 0.1); /* Inverted color for the selected item */
  border-color: #fff; /* Border color for the selected item */
}

.action-buttons {
  display: flex;
  justify-content: space-around;
  margin-top: 10px;
}

.action-buttons button {
  padding: 5px 10px;
  cursor: pointer;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}




